export default {
  props: ["reason","orderId","reload","changeVisibileModal","cancelUrl"],
  data() { 
	  return {
		  chooseData: null,
			detail: ''
	  }
  },
  methods: {
	  choose(data){
			this.chooseData = data;  
	  },
		cancel_order() {
			
			ut.request({
				data: {
					orderId: this.orderId,
					reasonId: this.chooseData,
					detail: this.detail
				},
				url: this.cancelUrl
			}).then(data=>{
				this.$parent.changeVisibileModal(false)
				this.$emit('reload');
				ut.totast("订单取消成功")
			})
		},
	  cancel_confirm() {
			if(!this.chooseData){
				ut.totast("请选择取消原因")
				return;
			}
			wx.showModal({
			title: '',
			content: '您确定要取消该订单吗？',
			success: (sm) => {
				if (sm.confirm) {
					// 用户点击了确定 可以调用删除方法了
						this.cancel_order();
					} else if (sm.cancel) {
					return;
					}
				}
			})
	  },
  }
}
